<template>
	<!-- <select
		:id="field_id"
		:name="field_id"
		v-model="value"
		class="form-select"
		multiple
		size="10"
		style="overflow: initial;"
	>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select> -->

	<p style="background-color: blue;">click here to open</p>
	<p style="background-color: red;">none</p>
	<p style="background-color: green;">all</p>
	<ul>
		<li
			v-for="(item, key) in field_data.enum"
		>
			{{ option_value(item) }} | {{ option_label(item) }}
		</li>
	</ul>

</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		data() {
			return {
				loading: false,
			}
		},
		created() {
		},
		methods: {
			// we have two types of enum options
			// one straight with key=>string (rokvyroby), other also with useless category data key=>object (auto_znacka)
			option_value(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return key;
				} else if (typeof value === 'object') {
					return value.value;
				}
			},
			option_label(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return value;
				} else if (typeof value === 'object') {
					return value.label;
				}
			},
		},
		computed: {
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					console.log('received:', STATE_params().get_form_value(this.field_id));
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					console.log('sent:', event_value);
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>
